@import url('https://fonts.googleapis.com/css?family=Audiowide|Sarpanch');
:root {
  --rowNum: 1;
  --colNum: 8;
}

* {
  font-family: Audiowide;
}

html {
  min-height: 100vh;
}

body {
  margin: 0;
  background-image: /* radial-gradient(ellipse at 30% 20%, #f0f0f0,#aaa 99%) */
   -webkit-linear-gradient(315deg, #a64df1 35%, #5359b4 70%, #1b7aa6) ,-webkit-radial-gradient(30% 20%, ellipse, #f0f0f0,#aaa 99%) ;
  background-image: /* radial-gradient(ellipse at 30% 20%, #f0f0f0,#aaa 99%) */
   -o-linear-gradient(315deg, #a64df1 35%, #5359b4 70%, #1b7aa6) ,-o-radial-gradient(30% 20%, ellipse, #f0f0f0,#aaa 99%) ;
  background-image: /* radial-gradient(ellipse at 30% 20%, #f0f0f0,#aaa 99%) */
   linear-gradient(135deg, #a64df1 35%, #5359b4 70%, #1b7aa6) ,radial-gradient(ellipse at 30% 20%, #f0f0f0,#aaa 99%) ;
   background-blend-mode: multiply;

}

