  padding: 0.25em;
  margin:  .5em;
  margin-top:  .75em;
  width: 100%;
  height: 90%;
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 0.25em;
  cursor:pointer;
}

.screenmenu {
  background: rgba(0,0,0,0.1);
  height: 100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  
}
.screenmenu.active {
  background: rgba(0,0,0,0.4);
}
.splash {
  width:100%;
  grid-column: 1 / 4;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size:2em;
  color: #246a99;    
  text-shadow: 1px 1px 0 white;
  opacity:0.5;
  -webkit-animation: loading 2.5s linear;
          animation: loading 2.5s linear;
  position:relative;
}
.splash:after {
  content:'';
  position:absolute;
  bottom:0.25em;
  background:#246a99;
  height:0.15em;
  width: 0%;
  -webkit-animation: load 2.5s linear;
          animation: load 2.5s linear;
}
@-webkit-keyframes load {
  100% { width:100%;  }
}
@keyframes load {
  100% { width:100%;  }
}
@-webkit-keyframes loading {
  100% { opacity:1;  }
}
@keyframes loading {
  100% { opacity:1;  }
}


